  overflow: hidden;
  width: 100% !important;
  display: flex;
  margin: 0 auto;
  height: 300px !important;
}

.fees-chart {
  display: flex;
  padding: 10px;
  background-color: #fff;
}
.fees-chart .chart {
  width: 120px;
  height: 120px;
}
.fees-chart .chart-labels {
  padding-left: 20px;
  padding-top: 0px;
}
.fees-chart .chart-labels ul {
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.fees-chart .chart-labels ul li {
  display: flex;
  flex: 0 0 1;
  margin-bottom: 10px;
}
.fees-chart .chart-labels ul li span {
  width: 10px;
  height: 10px;
  background-color: red;
  border-radius: 40%;
  margin-right: 7px;
  display: inline-block;
  margin-top: 5px;
}
.fees-chart .chart-labels ul li span.green {
  background-color: #10dc60;
}
.fees-chart .chart-labels ul li span.blue {
  background-color: blue;
}
.fees-chart .chart-labels ul li span.red {
  background-color: red;
}
.fees-chart .chart-labels ul li p {
  margin: 0;
  font-size: 12px;
  font-weight: 500;
  color: #000;
}
.fees-chart .chart-labels ul li p.text {
  font-size: 10px;
  color: #928eae;
}

ion-card {
  box-shadow: 0px 0px 3px 1px rgba(204, 204, 204, 0.7098039216);
  border-left: 3px solid #0cc052;
  margin-bottom: 5px;
}
ion-card ion-card-content {
  padding: 0 10px;
  margin-top: 5px;
  margin-bottom: 5px;
  line-height: 1.4;
  font-size: 11px;
  color: #fff;
}
ion-card ion-card-content ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
}
ion-card ion-card-content ul li {
  flex: 0 0 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 5px;
}
ion-card ion-card-content ul li .left {
  font-size: 12px;
  font-weight: 500;
  padding: 0;
  color: #000;
}
ion-card ion-card-content ul li .right {
  font-size: 13px;
  padding: 0;
  color: #000;
}
ion-card ion-card-content ul li .right.green {
  color: #fff;
}

.left {
  padding-left: 0 !important;
}

.yellow-bg-color {
  background-color: #fff !important;
  border-left: 0;
}

.total-fees {
  text-decoration: none;
  border-radius: 4px;
  background-color: #F97794;
  color: #000;
  padding: 3px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
  margin-bottom: 13px;
}

.download {
  margin-top: 10px;
}
.download a {
  text-decoration: none;
  border: 1px solid #092968;
  border-radius: 4px;
  background-color: #092968;
  color: #fff;
  padding: 3px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 13px;
}
.download a ion-icon {
  font-size: 16px;
}

.titles {
  text-align: center;
  font-size: 16px;
  color: #000;
  font-weight: 600;
  padding: 10px;
}

.particular {
  box-shadow: 0px 4px 8px 2px rgba(206, 204, 219, 0.48) !important;
  border: 0;
  border-radius: 8px;
  background: #623AA2;
  --background: #623AA2;
}
.particular ul li {
  margin-bottom: 0 !important;
  padding: 10px 5px;
  align-items: center;
}
.particular ul li .title {
  font-size: 16px;
  font-weight: 500;
  padding: 0;
  color: #fff;
}
.particular ul li .amount {
  font-size: 18px;
  padding: 0;
  color: #fff;
  font-weight: bold;
}
.particular ul li .amount.green {
  color: #0cc052;
}

.payments-wrap {
  background-color: rgba(2, 6, 197, 0.2588235294);
  padding-bottom: 30px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}
.payments-wrap .titles {
  color: #000 !important;
  font-weight: 600;
  padding: 15px 10px 15px !important;
  margin-top: 20px;
}
.payments-wrap .payments {
  box-shadow: 0 2px 1px 1px #4a25a3 !important;
  background: rgb(238, 206, 19);
  background: linear-gradient(180deg, rgb(238, 206, 19) 0%, rgb(178, 16, 255) 100%);
  border: 0;
  box-shadow: none;
  margin-bottom: 20px !important;
  margin-top: 0;
  border-radius: 8px;
  padding-bottom: 0px;
  position: relative;
  contain: none;
  overflow: visible;
}
.payments-wrap .payments ion-card-content {
  padding-top: 20px;
}
.payments-wrap .payments .header-title {
  background-color: #4a25a3;
  color: #fff;
  border-radius: 4px;
  position: absolute;
  padding: 2px 10px;
  top: -13px;
  left: 20px;
  font-size: 14px;
  text-transform: uppercase;
}
.payments-wrap .payments ul li {
  margin-bottom: 0 !important;
  padding: 5px 5px;
  align-items: center;
}
.payments-wrap .payments ul li .title {
  font-size: 14px;
  font-weight: normal;
  padding: 0;
  color: #fff;
}
.payments-wrap .payments ul li .amount {
  font-size: 14px;
  padding: 0;
  color: #fff;
  font-weight: 500;
}
.payments-wrap .payments .pay-now-btn2 {
  --background: radial-gradient(ellipse farthest-corner at right bottom, #FEDB37 0%, #FDB931 8%, #9f7928 30%, #8A6E2F 40%, transparent 80%),
  radial-gradient(ellipse farthest-corner at left top, #FFFFFF 0%, #FFFFAC 8%, #D1B464 25%, #5d4a1f 62.5%, #5d4a1f 100%);
}
.payments-wrap .payments .pay-now-btn {
  color: #000 !important;
  font-weight: bold;
  --background:#dddc3e;
